.wantToWork-area{
    background: #1A1A1A;
    position: relative;
    &::before{
        position: absolute;
        width: 100%;
        height: 5px;
        top: 0;
        content: "";
        background: #E61818;
    }

    // padding-bottom: 150px;
    // @media #{$md}{
    //     padding-bottom: 100px;
    // }
    // @media #{$sm}{
    //     padding-bottom: 70px;
    // }
    // @media #{$xs}{
    //     padding-bottom: 70px;
    // }
    .wantToWork-caption{
        h2{
            font-size: 30px;
            font-weight: 600;
            line-height: 1.5;
            margin-bottom: 12px;
            color: #fff;

            @media #{$md}{
                font-size: 30px; 
            }
            @media #{$sm}{
                font-size: 35px; 
            }
            @media #{$xs}{
                font-size: 25px; 
            }
        } 
        p{
            font-size: 18px;
            margin-bottom: 0;
            color: #DEDEDE;
        }
    }    
    .wantToWork-btn{

        @media #{$sm}{
            float: left;
            margin-top: 30px;
        }
        @media #{$xs}{
            float: left;
            margin-top: 30px;
        }
        @media #{$md}{
            // float: left;
            margin-top: 30px;
        }
    }
}
.sm-left{
    @media #{$xs}{
        float: left;
        margin-top: 30px;
    } 
    @media #{$sm}{
        float: left;
        margin-top: 30px;
    } 
}
.btn.w-btn {
	padding: 18px 65px 18px 33px;
}
.double-btn.f-right {
    @media #{$sm}{
        float: left;
    } 
    @media #{$xs}{
        float: left;
    } 
	
}

.wantToWork-btn img {
	margin-right: 9px;
}

.wants-wrapper {
    @include flexbox();
    blockquote {
        margin-right: 30px;
    }
}

.wantToWork-area {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
}